@font-face {
  font-family: 'Bahnschrift-Condensed';
  src: url(../../static/media/bahnschrift.ef5598ad.ttf);
}
.node-plan-page {
  padding: 0.9375rem;
}
.node-plan-page .wooden-token {
  display: flex;
  flex: 2 1;
}
.node-plan-page .wooden-token .left {
  margin-right: 0.9375rem;
}
.node-plan-page .wooden-token .left,
.node-plan-page .wooden-token .right {
  display: flex;
  flex: 1 1;
  justify-content: center;
  align-items: center;
  width: 10.3125rem;
  height: 6.875rem;
  border-radius: 0.3125rem;
  background: linear-gradient(-45deg, #2b2d37, #282b39);
  color: #ffffff;
}
.node-plan-page .wooden-token .left .top,
.node-plan-page .wooden-token .right .top {
  text-align: center;
}
.node-plan-page .wooden-token .left .top .big,
.node-plan-page .wooden-token .right .top .big {
  font-size: 2.8125rem;
}
.node-plan-page .wooden-token .left .top .bit,
.node-plan-page .wooden-token .right .top .bit {
  font-size: 0.75rem;
}
.node-plan-page .wooden-type {
  display: flex;
  flex-wrap: wrap;
  margin-top: 0.9375rem;
}
.node-plan-page .wooden-type .type-item:nth-child(1) {
  background: #1475F1;
  margin-right: 0.9375rem;
}
.node-plan-page .wooden-type .type-item:nth-child(2) {
  background: #14ABF1;
}
.node-plan-page .wooden-type .type-item {
  display: flex;
  flex: 1 1;
  text-align: center;
  color: #ffffff;
  justify-content: center;
  align-items: center;
  height: 2.75rem;
  border-radius: 0.3125rem;
}
.node-plan-page .wooden-type .type-item img {
  width: 1.25rem;
  height: 1.25rem;
  margin-right: 0.625rem;
}
.node-plan-page .wooden-bootom .type-item:nth-child(1) {
  background: linear-gradient(90deg, #d423f6, #25a4ff);
  margin-right: 0.9375rem;
}
.node-plan-page .wooden-bootom .type-item:nth-child(2) {
  background: linear-gradient(90deg, #5949ff, #ba1cf7);
}
.node-plan-page .node-plan {
  margin-top: 0.625rem;
}
.node-plan-page .node-plan .title {
  font-size: 0.875rem;
  font-weight: 12.5rem;
  line-height: 1.25rem;
  font-weight: 400;
  color: #ffffff;
}
.node-plan-page .node-plan .node-plan-item {
  position: relative;
  width: 100%;
  background: linear-gradient(-45deg, #2b2d37, #282b39);
  height: 6.25rem;
  border-radius: 0.3125rem;
  margin-top: 0.9375rem;
  padding-top: 0.9375rem;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
}
.node-plan-page .node-plan .node-plan-item .img {
  position: absolute;
  left: 0.9375rem;
  top: 0;
  width: 2.53125rem;
  height: 2.03125rem;
  background-image: url(/img/TOP.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
}
.node-plan-page .node-plan .node-plan-item .marginLeft {
  margin-left: 2.8125rem;
}
.node-plan-page .node-plan .node-plan-item .top {
  height: 1.875rem;
  position: relative;
  display: flex;
  justify-content: space-between;
  color: #ffffff;
}
.node-plan-page .node-plan .node-plan-item .top:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #eee;
  color: #eee;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.node-plan-page .node-plan .node-plan-item .top .des {
  font-size: 0.875rem;
  font-weight: 400;
}
.node-plan-page .node-plan .node-plan-item .top .btn {
  width: 4.0625rem;
  height: 1.25rem;
  line-height: 1.25rem;
  font-size: 0.6875rem;
  border-radius: 0.3125rem;
  background: #00d2ff;
  text-align: center;
  color: #FEFEFF;
}
.node-plan-page .node-plan .node-plan-item .bottom {
  display: flex;
  flex: 4 1;
  align-items: center;
  color: #ffffff;
  height: 3.125rem;
}
.node-plan-page .node-plan .node-plan-item .bottom .bottom-item:nth-child(3) {
  margin-right: 0rem;
}
.node-plan-page .node-plan .node-plan-item .bottom .bottom-item:nth-child(3) p {
  margin-left: 1.875rem;
}
.node-plan-page .node-plan .node-plan-item .bottom .bottom-item {
  flex: 1 1;
  margin-right: 0.9375rem;
}
.node-plan-page .node-plan .node-plan-item .bottom .bottom-item p:nth-child(1) {
  font-size: 1.1875rem;
  font-weight: 600;
  color: #00d2ff;
}
.node-plan-page .node-plan .node-plan-item .bottom .bottom-item p:nth-child(1) .little {
  font-size: 0.6875rem;
}
.node-plan-page .node-plan .node-plan-item .bottom .bottom-item p:nth-child(2) {
  font-size: 0.6875rem;
  font-weight: 400;
  color: #999999;
}
