@font-face {
  font-family: 'Bahnschrift-Condensed';
  src: url(../../static/media/bahnschrift.ef5598ad.ttf);
}
.my-assets-page {
  padding: 0.9375rem;
}
.my-assets-page .all-assets .title {
  display: flex;
  justify-content: space-between;
}
.my-assets-page .all-assets .title .left {
  font-size: 0.75rem;
  line-height: 1.5rem;
  font-family: PingFangSC-Regular;
  font-weight: 400;
  color: #666666;
}
.my-assets-page .all-assets .num {
  font-size: 1.875rem;
  font-family: Bahnschrift-Condensed;
  font-weight: 300;
  color: #ffffff;
}
.my-assets-page .all-assets .num .bit {
  font-size: 1.25rem;
  margin-left: 0.3125rem;
}
.my-assets-page .all-assets .assets-box {
  display: flex;
  margin-top: 0.78125rem;
  min-height: 6.40625rem;
}
.my-assets-page .all-assets .assets-box .border {
  border: 3px solid #ffffff;
}
.my-assets-page .all-assets .assets-box .my-wallet {
  flex: 1 1;
  background: linear-gradient(90deg, #0070ff, #00bfff);
  border: 3px solid 13141c;
  min-height: 5.3125rem;
  border-radius: 0.3125rem;
  margin-right: 0.9375rem;
  padding-top: 1.5625rem;
  padding-bottom: 1.5625rem;
}
.my-assets-page .all-assets .assets-box .my-wallet .top,
.my-assets-page .all-assets .assets-box .my-wallet .bottom {
  padding-left: 0.625rem;
  color: #fff;
}
.my-assets-page .all-assets .assets-box .my-wallet .bottom {
  font-size: 1.125rem;
  line-height: 1.875rem;
  font-family: Bahnschrift-Condensed;
  font-weight: normal;
  color: #ffffff;
}
.my-assets-page .all-assets .assets-box .thermal-account {
  flex: 1 1;
  border-radius: 0.3125rem;
  min-height: 5.3125rem;
  padding-top: 1.5625rem;
  padding-bottom: 1.5625rem;
  background: linear-gradient(90deg, #b32eff, #4f00ff);
  border: 3px solid 13141c;
}
.my-assets-page .all-assets .assets-box .thermal-account .top,
.my-assets-page .all-assets .assets-box .thermal-account .bottom {
  padding-left: 0.625rem;
  color: #fff;
}
.my-assets-page .all-assets .assets-box .thermal-account .bottom {
  font-size: 1.125rem;
  line-height: 1.875rem;
  font-family: Bahnschrift-Condensed;
  font-weight: normal;
  color: #ffffff;
}
.my-assets-page .all-assets .type-list {
  display: flex;
  flex: 4 1;
  text-align: center;
  margin-top: 1.25rem;
}
.my-assets-page .all-assets .type-list .list {
  flex: 1 1;
}
.my-assets-page .all-assets .type-list .list img {
  width: 1.25rem;
  height: 1.25rem;
}
.my-assets-page .all-assets .type-list .list .type {
  font-size: 0.75rem;
  line-height: 1.25rem;
  font-family: PingFangSC-Regular;
  font-weight: 400;
  color: #f9eeff;
}
.my-assets-page .all-assets .currency-list {
  margin-top: 0.9375rem;
}
.my-assets-page .all-assets .currency-list .list-top {
  height: 2.1875rem;
  line-height: 2.1875rem;
  font-family: 0.75rem;
  color: #ffffff;
  border-radius: 10px 10px 0px 0px;
}
.my-assets-page .all-assets .currency-list .list-item:nth-child(2n-1) {
  background: #1f2027;
}
.my-assets-page .all-assets .currency-list .list-item:nth-child(2n) {
  background: #16181e;
}
.my-assets-page .all-assets .currency-list .list-item {
  display: flex;
  flex: 4 1;
  text-align: center;
  height: 1.875rem;
  line-height: 1.875rem;
  font-family: 0.75rem;
  color: #ffffff;
}
.my-assets-page .all-assets .currency-list .list-item div {
  flex: 1 1;
}
.my-assets-page .all-assets .thermal-account-list {
  margin-top: 0.625rem;
}
.my-assets-page .all-assets .thermal-account-list .am-tabs-default-bar-content {
  background: #13141C;
  color: #fff !important;
  border-top: 1px solid #666666;
}
.my-assets-page .all-assets .thermal-account-list .thermal-list {
  width: 100%;
  margin-top: 0.9375rem;
  padding-top: 1.125rem;
  padding-bottom: 1.125rem;
  padding-left: 1rem;
  padding-right: 1rem;
  border: 2px solid #666666;
  background: linear-gradient(-30deg, #333333, #111111);
  position: relative;
}
.my-assets-page .all-assets .thermal-account-list .thermal-list .geTeamDeatilLeft {
  min-height: 1.125rem;
}
.my-assets-page .all-assets .thermal-account-list .thermal-list .getTeamDetail {
  position: absolute;
  right: 0.3125rem;
  min-width: 2.40625rem;
  min-height: 1.78125rem;
  text-align: right;
}
.my-assets-page .all-assets .thermal-account-list .thermal-list .thermal-title {
  display: flex;
  justify-content: space-between;
  color: #fff;
}
.my-assets-page .all-assets .thermal-account-list .thermal-list .thermal-title .ING {
  background: #16ba7e;
}
.my-assets-page .all-assets .thermal-account-list .thermal-list .thermal-title .END {
  background: #666666;
}
.my-assets-page .all-assets .thermal-account-list .thermal-list .thermal-title .status {
  padding-left: 0.625rem;
  padding-right: 0.625rem;
  margin-right: 0.3125rem;
  border-radius: 4px;
}
.my-assets-page .all-assets .thermal-account-list .thermal-list .hold-time .link {
  margin-left: 0.625rem;
  color: #00d2ff;
}
.my-assets-page .all-assets .thermal-account-list .thermal-list .lock-position,
.my-assets-page .all-assets .thermal-account-list .thermal-list .share-bonus,
.my-assets-page .all-assets .thermal-account-list .thermal-list .hold-time {
  display: flex;
  justify-content: space-between;
  color: #fff;
  margin-top: 1rem;
}
.redeem-modal .title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 2.5rem;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
  position: relative;
}
.redeem-modal .title:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #666666;
  color: #666666;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.redeem-modal .title .right {
  font-size: 0.875rem;
  color: #ffffff;
  font-weight: 400;
}
.redeem-modal .title .left {
  font-size: 0.5625rem;
  color: #ffffff;
  font-weight: 400;
}
.redeem-modal .price-detail {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.6875rem;
  height: 1.875rem;
  color: #999999;
}
.redeem-modal .price-detail .red {
  color: #FF3333;
}
.redeem-modal .offer-price-input {
  margin-top: 0.8125rem;
  background: rgba(255, 255, 255, 0);
}
.redeem-modal .offer-price-input .password .am-input-extra {
  color: #00D2FF !important;
}
.redeem-modal .offer-price-input .password .am-list-line {
  position: relative;
}
.redeem-modal .offer-price-input .password .am-list-line:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #fff;
  color: #fff;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.redeem-modal .offer-price-input .am-list-item {
  background: rgba(255, 255, 255, 0);
  padding-left: 1.5625rem;
  padding-right: 1.5625rem;
}
.redeem-modal .offer-price-input .am-list-item .am-list-line {
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
}
.redeem-modal .offer-price-input .am-list-item .am-list-line input {
  font-size: 0.75rem;
  font-weight: 400;
  color: #666666;
}
.redeem-modal .offer-price-input .am-list-item .am-list-line .am-input-extra {
  color: #ffffff;
}
.redeem-modal .des-tip {
  display: flex;
  justify-content: flex-end;
  padding-right: 1.5625rem;
  font-size: 0.5625rem;
  color: #00D2FF;
}
.redeem-modal .des-tip .red {
  color: #FF3333 !important;
}
.redeem-modal .footerButton {
  margin-top: 2.5rem;
  margin-bottom: 3.125rem;
  padding-left: 1.5625rem;
  padding-right: 1.5625rem;
}
